import React, { useState, useCallback } from 'react'
import DeckGL from '@deck.gl/react'
import { MVTLayer } from '@deck.gl/geo-layers'
import { StaticMap } from 'react-map-gl'
import MatGeocoder from 'react-mui-mapbox-geocoder'

import { MapView } from '@deck.gl/core'
import { TileLayer } from '@deck.gl/geo-layers'
import { BitmapLayer, PathLayer } from '@deck.gl/layers'

// Viewport settings
const INITIAL_VIEW_STATE = {
  longitude: -0.03,
  latitude: 51.49,
  zoom: 9,
  pitch: 0,
  bearing: 0,
  minZoom: 9,
  maxZoom: 17
}

const devicePixelRatio =
  (typeof window !== 'undefined' && window.devicePixelRatio) || 1

function getTooltip({ tile }) {
  return tile && `tile: x: ${tile.x}, y: ${tile.y}, z: ${tile.z}`
}

export default function App({ showBorder = false, onTilesLoad = null }) {
  const MAPBOX_TOKEN =
    'pk.eyJ1IjoiZ2xhLWdpcyIsImEiOiJjanBvNGh1bncwOTkzNDNueWt5MGU1ZGtiIn0.XFxLdq2dXttcXSXTiREPTA'

  const getTooltip = ({ object }) => {
    return (
      object && {
        html: `<pre>${JSON.stringify(object.properties.aod, null, '  ')}</pre>`
      }
    )
  }

  // 'aod_prop' : { variable: "Access to Public Open Space", Value : "aod_prop", displayValue: "aod", displayUnit: "% pop", averageLondon: "aod_lon_av", thresholdValue: "aod_th_val", thresholdValueProp: "aod_th_val_prop",  hoverInfo: "Proportion of the population that lives within an Area of Deficiency in Access to Local and District Public Open Space. Area of Deficiency is defined by Greenspace Information for Greater London CIC based on distances to public open space in the London Plan.<br><br>Threshold is based on population in an AoD greater than the London average."},

  const LLayer = new MVTLayer({
    data: `https://circular-attraction.glitch.me/data/aod-hexes/{z}/{x}/{y}.pbf`,
    minZoom: 9,
    maxZoom: 17,
    getLineColor: [32, 32, 32],
    getFillColor: d => {
      const val = d.properties.aod_prop * 192
      return [255, 255, 255, val]
    },
    pickable: true
  })

  const [viewState, setViewState] = useState(INITIAL_VIEW_STATE)

  const onViewStateChange = useCallback(({ viewState }) => {
    // Manipulate view state
    // viewState.target[0] = Math.min(viewState.target[0], 10)
    // Save the view state and trigger rerender
    setViewState(viewState)
  }, [])

  const geocoderApiOptions = {
    country: 'gb',
    proximity: { longitude: 0, latitude: 51.49 },
    bbox: [-0.489, 51.28, 0.236, 51.686]
  }

  const onSelectHandler = result => {
    setViewState({
      longitude: result.center[0],
      latitude: result.center[1],
      zoom: 14,
      transitionDuration: 400
      // transitionInterpolator: new FlyToInterpolator(),
      // transitionEasing: easeCubic
    })
  }

  return (
    <DeckGL
      layers={[LLayer]}
      views={new MapView({ repeat: true })}
      // initialViewState={INITIAL_VIEW_STATE}
      viewState={viewState}
      onViewStateChange={onViewStateChange}
      controller={true}
      getTooltip={getTooltip}
    >
      <MatGeocoder
        inputPlaceholder="Search Address"
        accessToken={MAPBOX_TOKEN}
        onSelect={onSelectHandler}
        showLoader={true}
        {...geocoderApiOptions}
      />{' '}
      <StaticMap
        reuseMaps
        mapStyle="os_night.json"
        preventStyleDiffing={true}
        mapboxApiAccessToken={MAPBOX_TOKEN}
      />
    </DeckGL>
  )
}
